.basition-container {
    position: relative;
    width: 3250px;
    height: 1050px;
    margin: 0 auto;
    background: url(./images/bg.png) no-repeat center / 100% 100%;
    >.title {
        position: absolute;
        top: 34px;
        left: 340px;
        font-size: 40px;
        color: #FFF;
    }
    .count {
        display: flex;
        position: absolute;
        top: 20px;
        left: 800px;
        width: 800px;
        height: 78px;
        line-height: 78px;
        padding: 0 20px;
        box-sizing: border-box;
        background: url(../../images/count-bg.png) no-repeat center / 100% 100%;
        cursor: pointer;
        span:nth-child(1) {
            font-size: 32px;
            color: #FFF;
        }
        span:nth-child(2) {
            margin-left: 50px;
            font-size: 60px;
            color: #fdd100;
            text-shadow: 0 0 30px #fdd100;
        }
    }
}

.basition-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -47%);
    width: 3106px;
    height: 890px;
    padding: 18px 32px;
    box-sizing: border-box;
    background: url(./images/wrapper-bg.png) no-repeat center / 100% 100%;
    .log-container {
        width: 100%;
        .title {
            position: relative;
            width: 100%;
            height: 60px;
            font-size: 40px;
            color: #36ebff;
            &:after {
                display: block;
                position: absolute;
                bottom: 0;
                content: '';
                width: 100%;
                height: 10px;
                background: url(./images/title-bg.png) no-repeat center / 100% 100%;
            }
        }
        .log-table {
            width: 100%;
            height: 286px;
            margin-top: 18px;
            background: url(./images/log-table-bg.png) no-repeat center / 100% 100%;
            .header {
                display: flex;
                width: 100%;
                height: 54px;
                margin-bottom: 14px;
                li {
                    width: 14%;
                    height: 54px;
                    line-height: 54px;
                    font-size: 30px;
                    color: #FFF;
                    text-align: center;
                }
            }
            .row {
                width: 100%;
                height: 192px;
                overflow: hidden;
                .col {
                    display: flex;
                    position: relative;
                    width: 100%;
                    height: 64px;
                    &:after {
                        display: block;
                        position: absolute;
                        bottom: 0;
                        content: '';
                        width: 100%;
                        height: 2px;
                        background: url(./images/log-table-border.png) no-repeat center / 100% 100%;
                    }
                    li {
                        width: 14%;
                        height: 64px;
                        line-height: 64px;
                        font-size: 26px;
                        color: #FFF;
                        text-align: center;
                    }
                    li:nth-child(3),
                    li:nth-child(4) {
                        color: #ffd200;
                    }
                    li:nth-child(7) {
                        color: #42ffb1;
                    }
                }
            }
        }
    }
    .trace-source-container {
        display: flex;
        align-items: center;
        width: 100%;
        height: 460px;
        margin-top: 16px;
        .title {
            height: 60px;
            font-size: 40px;
            color: #36ebff;
            text-shadow: 0 0 30px #36ebff;
        }
        .link {
            width: 194px;
            height: 234px;
            background: url(./images/link.png) no-repeat center / 100% 100%;
        }
        .visit-top5-container {
            width: 1066px;
            height: 460px;
            .bar-chart {
                width: 100%;
                height: 400px;
            }
        }
        .visiter-table {
            width: 624px;
            height: 100%;
            .header {
                display: flex;
                height: 90px;
                line-height: 90px;
                li {
                    width: 33.3333%;
                    font-size: 32px;
                    color: #9dc9ff;
                    text-align: center;
                }
            }
            .row {
                width: 100%;
                height: 370px;
                overflow: auto;
                background: url(./images/visiter-table-bg.png) no-repeat center / 100% 100%;
                /*Webkit自定义滚动条样式start*/
                &::-webkit-scrollbar {  /* 滚动条整体部分 */
                    width:5px;
                    margin-right:2px
                }
                &::-webkit-scrollbar:horizontal {
                    height:0;
                }
                &::-webkit-scrollbar-track {  /* 外层轨道 */
                    border-radius: 0;
                }
                &::-webkit-scrollbar-track-piece {  /*内层轨道，滚动条中间部分 */
                    background-color: transparent;
                    border-right: 1px solid #339dff;
                    border-radius: 0;
                }
                &::-webkit-scrollbar-thumb {  /* 滑块 */
                    width:7px;
                    border-radius: 0;
                    background: #fff;
                }
                &::-webkit-scrollbar-corner { /* 边角 */
                    width: 0;
                    background-color: transparent;
                }
                &::-webkit-scrollbar-thumb:hover { /* 鼠标移入滑块 */
                    background: #bbb;
                }
                .col {
                    display: flex;
                    cursor: pointer;
                    width: 100%;
                    height: 74px;
                    line-height: 74px;
                    &:hover,
                    &.emphasis {
                        background: url(./images/visiter-table-emphasis.png) no-repeat center / 100% 100%;
                        li:nth-child(3) {
                            color: #ffd200;
                        }
                    }
                    li {
                        width: 33.3333%;
                        font-size: 26px;
                        color: #FFF;
                        text-align: center;
                    }
                }
            }
        }
        .trace-container {
            width: 926px;
            height: 100%;
            .tree-chart {
                width: 100%;
                height: 400px;
                canvas {
                    border: none;
                    outline: none;
                }
            }
        }
    }
}



