.database-container {
    position: relative;
    z-index: 1;

    width: 3250px;
    height: 640px;
    margin: 0 auto;
    background: url(./images/bg.png) no-repeat center / 100% 100%;
    >.title {
        position: absolute;
        top: 34px;
        left: 340px;
        font-size: 40px;
        color: #FFF;
    }
    .count {
        display: flex;
        position: absolute;
        top: 20px;
        left: 800px;
        width: 800px;
        height: 78px;
        line-height: 78px;
        padding: 0 20px;
        box-sizing: border-box;
        background: url(../../images/count-bg.png) no-repeat center / 100% 100%;
        span:nth-child(1) {
            font-size: 32px;
            color: #FFF;
        }
        span:nth-child(2) {
            margin-left: 50px;
            font-size: 60px;
            color: #fdd100;
            text-shadow: 0 0 30px #fdd100;
        }
    }
    .content-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -45%);
        width: 96%;
        height: 430px;
        padding: 0 26px;
        box-sizing: border-box;
        background: url(./images/content-wrapper-bg.png) no-repeat center / 100% 100%;
        .visit-top5-container {
            position: relative;
            width: 900px;
            height: 360px;
            padding: 8px 24px;
            box-sizing: border-box;
            background: url(./images/visit-top5-bg.png) no-repeat center / 100% 100%;
            .title {
                font-size: 40px;
                color: #36ebff;
                text-shadow: 0 0 30px #36ebff;
            }
            .gauge-group {
                display: flex;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -40%);
                width: 92%;
                height: 200px;
                .gauge-item {
                    width: 20%;
                    height: 100%;
                    .value {
                        font-size: 26px;
                        color: #FFF;
                        text-align: center;
                    }
                    .chart {
                        width: 100%;
                        height: 120px;
                        margin-top: 20px;
                    }
                    .ip {
                        font-size: 20px;
                        color: #a5b0ff;
                        text-align: center;
                        padding-top: 16px;
                        margin-top: -20px;
                        border-top: 1px solid #637dff;
                    }
                }
            }
        }
        .model-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 2130px;
            height: 360px;
            padding: 8px 46px;
            box-sizing: border-box;
            background: url(./images/model-bg.png) no-repeat center / 100% 100%;
            .model-wrapper {
                .title {
                    font-size: 40px;
                    color: #36ebff;
                    text-shadow: 0 0 30px #36ebff;
                }
                .model-list {
                    width: 480px;
                    height: 280px;
                    .model-item {
                        position: relative;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin-top: 12px;
                        .icon {
                            cursor: pointer;
                            width: 58px;
                            height: 58px;
                        }
                        .dialog {
                            position: absolute;
                            top: 0;
                            left: -354px;
                            width: 324px;
                            height: auto;
                            padding: 24px;
                            box-sizing: border-box;
                            background: url(./images/dialog-bg.png) no-repeat center / 100% 100%;
                            &.hide {
                                display: none;
                            }
                            p {
                                line-height: 42px;
                                font-size: 26px;
                                color: #FFF;
                                text-indent: 52px;
                            }
                        }
                        .content {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            position: relative;
                            cursor: pointer;
                            width: 414px;
                            height: 66px;
                            background: url(./images/sprites.png) -10px -598px;
                            &.emphasis {
                                background: url(./images/sprites.png) -10px -684px;
                            }
                            .bar {
                                position: relative;
                                width: 280px;
                                height: 16px;
                                padding: 0 8px;
                                box-sizing: border-box;
                                background: #0b2a47;
                                span {
                                    display: block;
                                    height: 100%;
                                    margin: 0 auto;
                                }
                                span:nth-child(1) {
                                    width: 100%;
                                    background: url(./images/progress.png) no-repeat center / 100% 100%;
                                }
                                span:nth-child(2) {
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                    background: #0b2a47;
                                }
                            }
                            .text {
                                margin-top: 8px;
                                font-size: 26px;
                                color: #b0caf9;
                            }
                            .value {
                                position: absolute;
                                top: 50%;
                                right: 40px;
                                transform: translateY(-50%);
                                font-size: 32px;
                                color: #3da3ff;
                            }
                        }
                    }
                    .model-item:nth-child(1) {
                        .icon {
                            background: url(./images/sprites.png) -10px -302px;
                            &.emphasis {
                                background: url(./images/sprites.png) -10px -10px;
                            }
                        }
                    }
                    .model-item:nth-child(2) {
                        .icon {
                            background: url(./images/sprites.png) -10px -376px;
                            &.emphasis {
                                background: url(./images/sprites.png) -10px -83px;
                            }
                        }
                    }
                    .model-item:nth-child(3) {
                        .icon {
                            background: url(./images/sprites.png) -10px -450px;
                            &.emphasis {
                                background: url(./images/sprites.png) -10px -156px;
                            }
                        }
                    }
                    .model-item:nth-child(4) {
                        .icon {
                            background: url(./images/sprites.png) -10px -524px;
                            &.emphasis {
                                background: url(./images/sprites.png) -10px -229px;
                            }
                        }
                    }
                }
            }
            .model-table {
                position: relative;
                width: 1500px;
                height: 320px;
                .com-hint {
                    color: #70eb12;
                }
                table {
                    width: 100%;
                    height: 100%;
                    margin-top: 24px;
                    .header {
                        font-size: 32px;
                        color: #9dc9ff;
                    }
                    .row {
                        font-size: 26px;
                        color: #FFF;
                        height: 68px;
                        line-height: 68px;
                        &:hover {
                            background: url(./images/table-row-emphasis.png) no-repeat center / 100% 100%;
                        }
                        th:nth-child(3),
                        th:nth-child(4),
                        th:nth-child(8) {
                            color: #fdd100;
                        }
                    }
                }
            }
        }
    }
}



